/* Colors */
$beige: #dfa76e;

//  
$darkGray: #202020;
$red: #99303B;
$white: #f7f7f7;
$purple: #654178;
$gray: #eaeaea;
$green: #6d867a;

$nightBlack: #3f3c39;
$maroon: #952d2c;
$hover-link: #bd0d0d;
$cyan: #76c1e2;
$viole: #a86fb9;
$shadow: rgba(0, 0, 0, .4);
$ghostBlack:  rgba(29, 22, 16, .8);

.color {
    &_white {
        color: $white;
    }

    &_green {
        color: $green;
    }

    &_gray {
        color: #a5a5a5;
    }
}